  line-height: 17px;
  text-decoration: none;
  line-height: 36px;
}

.menu__link .menu__item:not(:last-of-type) {
  padding-right: 10px;
}

.menu__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu__item.menu__list_arrow {
  padding-right: 24px;
}

.menu > .menu__list > .menu__item.menu__item:not(:last-of-type) {
  margin-right: 30px;
}
.menu > .menu__list > .menu__item.menu__item:not(:last-of-type).menu__item_sub-menu {
  margin-right: 20px;
}

.menu__list:first-child > .menu__item:last-child > .menu__link {
  padding-right: 0px;
}

.menu__list_arrow {
  padding-right: 0px;
}

.menu__link + .menu__list {
  position: absolute;
  min-width: 150px;
  display: none;
  border-radius: 3px;
  box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.07);
}

.menu__link + .menu__list > .menu__item {
  padding-left: 10px;
}

.menu__link + .menu__list .menu__list {
  position: absolute;
  top: -1px;
  right: -150px;
}

.menu__link + .menu__list .menu__item {
  position: relative;
}

.menu__list_arrow {
  position: relative;
}

.menu__list_arrow:after {
  content: "";
  width: 24px;
  font-family: "Material Icons";
  text-align: center;
  vertical-align: middle;
  font-size: 24px;
  line-height: 20px;
  bottom: 6px;
  right: 2px;
  color: rgba(31, 32, 65, 0.5);
}

.menu__link_bold {
