  transform: translateX(0);
}

.button {
  border: none;
  font-family: Montserrat;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  background: none;
  color: #bc9cff;
  margin: 0px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: uppercase;
}

.button_simple {
  padding: 0;
}

.button_bordered {
  position: relative;
  background: transparent;
  border-radius: 22px;
  z-index: 1;
}

.button_bordered:before,
.button_bordered:after {
  content: " ";
  position: absolute;
  border-radius: 22px;
}

.button_bordered:before {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -2;
  background: #bc9cff;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}

.button_bordered:after {
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  background-color: white;
  z-index: -1;
}

.button_background_colored {
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border-radius: 22px;
  color: #ffffff;
}

.button_arrow {
  padding: 15px 20px;
  width: 100%;
  height: 44px;
  position: relative;
}

.button_arrow:after {
  font-family: "Material Icons";
  content: "arrow_forward";
  position: absolute;
  right: 10px;
  font-weight: normal;
  font-size: 24px;
  text-transform: none;
}

.button.button_simple.button_disabled {
  color: rgba(31, 32, 65, 0.5);
}

.button.button_bordered.button_disabled:before {
  background: linear-gradient(180deg, #bc9cff7f 0%, #8ba4f97f 100%);
}

.button.button_background_colored.button_disabled {
  background: linear-gradient(180deg, #bc9cff7f 0%, rgba(139, 164, 249, 0.5) 100%);
}

.button_dynamic-height {
  height: 100%;
  padding-top: 0;
  padding-bottom: 0;
}

.button_dynamic-width {
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}

